.footer {
  display: flex;
  flex-wrap: wrap;
  background-color: #FFF5F6;
  padding-left: 20px;
  padding-right: 20px;
  min-width: 320px;
  
  @media screen and (min-width: 768px) {
  padding-left: 36px;
  padding-right: 36px;
  }

  @media screen and (min-width: 1280px) {
  padding-left: 114px;
  padding-right: 116px;
  }
}
.footer__box {
  width: 100%;
  @media screen and (min-width:768px) {
      display: flex;

  }
}

.footer__box-title {
  width: auto;
  padding-bottom: 43px;

  @media screen and (min-width:768px) {
    width: 50%;
    padding-bottom: 91px;
  }

    @media screen and (min-width:1280px) {
          padding-bottom: 97px;

  }

  
}
.footer__box-social {
  width: auto;
  @media screen and (min-width:768px) {
    width: 50%;
  }
  
}


.footer__title {
  display: block;
  font-family: Titan One;
font-size: 18px;
line-height: 21px;
color: #FFA5BA;
width: 232px;
margin: 0;
// margin-right: auto;


@media screen and (min-width: 768px) {
  font-size: 26px;
line-height: 30px;
letter-spacing: 0.04em;
width: 340px;
}
@media screen and (min-width: 1280px) {
  font-size: 30px;
line-height: 34px;
letter-spacing: 0.04em;
width: auto;
}

}

.footer__title-decor {
  font-family: Titan One;
font-size: 34px;
line-height: 39px;
letter-spacing: 0.04em;
color: #D41443;
display: block;
width: 200px;
margin-top: 17px;

@media screen and (min-width: 768px) {
font-size: 48px;
line-height: 55px;
letter-spacing: 0.06em;
margin-top: 16px;
width: 340px;
}

@media screen and (min-width: 1280px) {
font-size: 58px;
line-height: 66px;
margin-top: 15px;
width: auto;
}



}

.social {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  // padding-top: 43px;
  padding-bottom: 46px;
  padding-left: 0;


  @media screen and (min-width: 768px) {
    padding-top: 0;
//     padding-left: auto;
    // width: 162px;
  }

    @media screen and (min-width: 1280px) {
    padding-bottom: 50px;

  }

}

.social__item {
  width: 32px;
  height: 32px;
  background-color:  #FEA5BB;
  border-radius: 50%;
  margin-right: 28.24px;
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

&:hover,
  &:focus {
      background-color:  #D41443;

  }

&:last-child {
  margin-right: 0;

  
}

@media screen and (min-width: 768px) {
  width: 34px;
  height: 34px;
  margin-right: 30px;
}

@media screen and (min-width: 1280px) {
  margin-right: 24px;

}

}

.contact {
  display: flex;
justify-content: flex-end;
  text-decoration: none;
  font-family: DM Sans;
font-weight: bold;
font-size: 12px;
line-height: 22px;
color: #D41443;
padding-bottom: 43px;

@media screen and (min-width: 768px) {
  font-size: 14px;
line-height: 26px;
padding-bottom: 0;
}

@media screen and (min-width: 1280px) {
  font-size: 16px;
line-height: 30px;
letter-spacing: 0.02em;

}

}

.contact__text {
color: #000000;
}

.founded {
  display: flex;
  justify-content: center;
  width: 100%;
  // align-items: center;
  // display: block;
  // position: relative;


  &::after {
    content: ' ';
    position: absolute;
    
    width: 100%;
    border: 1px solid #E1E1E1;
    margin-left: -20px;
    margin-right: -20px;

  }
}

.founded__text {
    text-align: center;
font-weight: bold;
font-size: 14px;
line-height: 21px;
color: #907E82;
width: 150px;
margin: 0;
padding-top: 22px;
padding-bottom: 25px;

@media screen and (min-width: 768px) {
  line-height: 26px;
  width: auto;
  letter-spacing: 0.02em;
  padding-top: 29px;
  padding-bottom: 18px;
  // text-align: left;
}

@media screen and (min-width: 1280px) {
  padding-top: 9px;
    padding-bottom: 10px;

}

}







